.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  font-weight: 600;
  box-shadow: 0 6px 18px rgba(15, 23, 42, 0.06);
  transition: background-color .15s ease, transform .15s ease;
  min-height: 40px;
  gap: .6rem;
